.slider {
  position: absolute;
  width: 100%;
  height: 80vh;
}
.section_sliders {
  width: 55%;
  height: 80vh;
  overflow: hidden;
  margin-top: 17vh;
 }
 
 .block_sliders {
   width: 500%;
   height: 100%;
   display: flex;
   margin-left: 0;
   
   transition: margin-left 1s cubic-bezier(0, .5, .5, 1);
 }
 
 .block_slider1,
 .block_slider2,
 .block_slider3,
 .block_slider4,
 .block_slider5 {
   width: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   background-size: cover;
   box-shadow: 0 0 10px #020022;  
 }
 
 .block_slider1 {
 background-image: url(/images/slide1.jpg)
 }
 
 .block_slider2 {
   background-image: url(/images/slide2.jpg);
   }
 .block_slider3 {
     background-image: url(/images/slide3.jpg);
   }
 .block_slider4 {
     background-image: url(/images/slide4.jpg);
   }
 .block_slider5 {
     background-image: url(/images/slide5.jpg);
   }

