.popup {
    width: 120%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #020035;
    position: absolute;
    margin-left: 15%;
    box-shadow: 0 0 10px #020022;
    display: none;
    
}

.popup_text {
    font-size: 90px;
    line-height: 1.5;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    padding: 5vh;
    text-align: center;
}
.popup_close {
    width: 40px;
    height: 40px;
    border-radius: 40px;
    position: absolute;
    z-index: 1;
    margin-left: 95%;
    cursor: pointer;
}
.popup_close:before {
    content: '+';
    color: #fff;
    position: absolute;
    z-index: 2;
    transform: rotate(45deg);
    font-size: 50px;
    line-height: 1;
    top: -5px;
    left: 6px;
    transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85);
}
.popup_close:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background: #ded3f5;
    z-index: 1;
    transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85);
    transform: scale(0.01);
}
.popup_close:hover:after {
    transform: scale(1);
}
.popup_close:hover:before {
    transform: scale(0.8) rotate(45deg);
    color: #fff;
}