.сontacts {
    display: flex;
}

.contacts_left {
    width: 50%;
    position: relative;
}

form {
    width: 120%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #020035;
    position: absolute;
    margin-left: 15%;
    box-shadow: 0 0 10px #020022;
}

.form_box {
    width: 75%;
    display: flex;
    flex-direction: column;
}    

.input_name,
.input_tel,
.input_mail,
.input_company,
.input_message,
.input_submit {
    width: 100%;
    margin-top: 4vh;
}
input {
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    outline: none;
}

p {
    font-size: 16px;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    padding-bottom: 1vh;
}

input,
.button {
    width: 100%;
    height: 4vh;
    border: none;
}

textarea {
    width: 100%;
    height: 15vh;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    border: none;
}

.button {
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    background-color: red;
    border: none;
    outline: none;
}

.erorr_tel,
.erorr_email {
    font-size: 18px;
    color: red;
    font-family: 'Montserrat', sans-serif;
    display: none;
}

.contacts_right {
    width: 40%;
    display: flex;
    flex-direction: column;
    margin-top: 35vh;
    margin-left: 50%;
    position: absolute;
}

.contacts_right_title {
    font-size: 40px;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
}

.content_right_text {
    font-size: 26px;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
}